body {
    background-color: #000;
}

.intro-section {
    align-content: center;
    display: flex;
    flex-direction: column;
}

.intro-section h1, h3, p {
    text-align: left;
    color: #fff;
    font-family: 'Merienda', cursive;
}

.intro-section {
    min-height: 100vh;
    min-height: calc(var(--vh, 1vh) * 100);
    width: 100vw;

}

.title-div {
    flex-grow: 1;
    align-self: flex-start;
    margin-top: 1em;
    margin-left: 1em
}

.main-title {
    color: #0091EA !important;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 0;
}


.main-sub-title {
    margin-top: 0;
}

.intro-paragraph-container {
    flex-grow: 2;
    display: flex;
    min-height: 300px;
    justify-content: center;
    align-content: center;
}

div.Typist {
    align-self: center;
    height: 300px;
    max-width: 50%;
    width: 50%;
    margin: 0;
}

.intro-paragraph {
    text-align: left;
}

.arrow-down-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.icon-box {
    min-height: 90px;
    display: flex;
    justify-content: center;
}

.arrow-down {
    visibility: visible;
    margin-bottom: 1.5em;
    align-self: flex-end;
    transition: visibility 1.5s ease, margin 1s ease;
    fill: #ffeb7f;
}

.arrow-down:hover {
    cursor: pointer;
    transition: margin 0.8s ease;
    margin-bottom: 1em;
    fill: #ffeb7f;
}

.arrow-down-1:hover {
    transition: margin 0.8s ease;
    margin-bottom: 1em;
    fill: #ffeb7f;
}

.contact-section {
    min-height: 100vh !important;
    min-height: calc(var(--vh, 1vh) * 100) !important;
    display: flex;
    flex-direction: column;
    background-color: #0091EA;
    justify-content: center;
}

.contact-section h1 {
    color: rgb(210, 54, 105) !important;
    font-family: 'Merienda', cursive;
}

.work-title {
    text-align: center;
}

.gone {
    transition: visibility 1.5s ease;
    visibility: hidden;
}

.form-container {
    flex-grow: 1;
    display: flex;
    justify-content: center;
}

.contact-form {
    width: 50%;
    display: flex;
    flex-direction: column;
}

.contact-form input {
    border-radius: 0.25em;
    font-size: 1.1em;
    margin-bottom: 0;
    padding: 0.5em;
}

.input-error {
    border-color: #FF3D00;
}

.contact-form textarea {
    margin-bottom: 0;

    border-style: inset;
    font-size: 1.1em;
    border-radius: 0.25em;
    padding: 0.5em;
    height: 12em;
}

.form-element {
    margin-bottom: 1em;
    display: flex;
    flex-direction: column;
}

.contact-form label {
    font-family: 'Merienda', cursive;
    font-weight: bold;
    /*color: rgb(210, 54, 105) !important;*/
    color: #fff !important;
}

.submit-container {
    display: flex;
    justify-content: flex-end;
}

.contact-form button {
    cursor: pointer;
    align-self: flex-end;
    background-color: #ffeb7f;
    border-radius: 0.25em;
    width: 10em;
    font-size: 1.1em;
    font-family: 'Merienda', cursive;
}

.contact-form button:hover {
    background-color: #ffe766;
}

.contact-section-header {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
    text-align: center;
}

.contact-section-header h1 {
    margin-bottom: 1rem;
    color: #fff !important;
}

.social {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
    text-align: center;
}

.github-icon {
    cursor: pointer;
    align-self: flex-end;
    margin-bottom: 1rem;
    transition: all 0.25s ease;
    font-size: 100%;
}

.github-icon:hover {
    transition: all 0.25s ease;
    font-size: 110%;
}


.spinner {
    align-self: center;
    margin-right: 1rem;
    animation-name: spin;
    animation-duration: 2000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.error-label {
    margin-top: 0.1rem;
}

.toast {
    font-family: 'Merienda', cursive !important;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}


@media only screen and (min-width: 600px) and (max-width: 1149px) {
    .contact-form {
        width: 75%;
    }

    div.Typist {
        max-width: 75%;
        width: 75%;
    }
}

@media only screen and (min-width: 100px) and (max-width: 599px) {
    .contact-form {
        width: 100%;
    }

    .intro-paragraph-container {
        margin: 0 1rem 0 1rem;
    }


    div.Typist {
        max-width: 100%;
        width: 100%;
    }

    .form-container {
        margin: 0 1rem 0 1rem;
    }
}

