body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  overflow: scroll;
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex;
  width: 100vw;
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);

}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: #000;
}

.intro-section {
    -webkit-align-content: center;
            align-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.intro-section h1, h3, p {
    text-align: left;
    color: #fff;
    font-family: 'Merienda', cursive;
}

.intro-section {
    min-height: 100vh;
    min-height: calc(1vh * 100);
    min-height: calc(var(--vh, 1vh) * 100);
    width: 100vw;

}

.title-div {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-align-self: flex-start;
            align-self: flex-start;
    margin-top: 1em;
    margin-left: 1em
}

.main-title {
    color: #0091EA !important;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 0;
}


.main-sub-title {
    margin-top: 0;
}

.intro-paragraph-container {
    -webkit-flex-grow: 2;
            flex-grow: 2;
    display: -webkit-flex;
    display: flex;
    min-height: 300px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}

div.Typist {
    -webkit-align-self: center;
            align-self: center;
    height: 300px;
    max-width: 50%;
    width: 50%;
    margin: 0;
}

.intro-paragraph {
    text-align: left;
}

.arrow-down-container {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.icon-box {
    min-height: 90px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.arrow-down {
    visibility: visible;
    margin-bottom: 1.5em;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    -webkit-transition: visibility 1.5s ease, margin 1s ease;
    transition: visibility 1.5s ease, margin 1s ease;
    fill: #ffeb7f;
}

.arrow-down:hover {
    cursor: pointer;
    -webkit-transition: margin 0.8s ease;
    transition: margin 0.8s ease;
    margin-bottom: 1em;
    fill: #ffeb7f;
}

.arrow-down-1:hover {
    -webkit-transition: margin 0.8s ease;
    transition: margin 0.8s ease;
    margin-bottom: 1em;
    fill: #ffeb7f;
}

.contact-section {
    min-height: 100vh !important;
    min-height: calc(1vh * 100) !important;
    min-height: calc(var(--vh, 1vh) * 100) !important;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #0091EA;
    -webkit-justify-content: center;
            justify-content: center;
}

.contact-section h1 {
    color: rgb(210, 54, 105) !important;
    font-family: 'Merienda', cursive;
}

.work-title {
    text-align: center;
}

.gone {
    -webkit-transition: visibility 1.5s ease;
    transition: visibility 1.5s ease;
    visibility: hidden;
}

.form-container {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.contact-form {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.contact-form input {
    border-radius: 0.25em;
    font-size: 1.1em;
    margin-bottom: 0;
    padding: 0.5em;
}

.input-error {
    border-color: #FF3D00;
}

.contact-form textarea {
    margin-bottom: 0;

    border-style: inset;
    font-size: 1.1em;
    border-radius: 0.25em;
    padding: 0.5em;
    height: 12em;
}

.form-element {
    margin-bottom: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.contact-form label {
    font-family: 'Merienda', cursive;
    font-weight: bold;
    /*color: rgb(210, 54, 105) !important;*/
    color: #fff !important;
}

.submit-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.contact-form button {
    cursor: pointer;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    background-color: #ffeb7f;
    border-radius: 0.25em;
    width: 10em;
    font-size: 1.1em;
    font-family: 'Merienda', cursive;
}

.contact-form button:hover {
    background-color: #ffe766;
}

.contact-section-header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    text-align: center;
}

.contact-section-header h1 {
    margin-bottom: 1rem;
    color: #fff !important;
}

.social {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    text-align: center;
}

.github-icon {
    cursor: pointer;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin-bottom: 1rem;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    font-size: 100%;
}

.github-icon:hover {
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    font-size: 110%;
}


.spinner {
    -webkit-align-self: center;
            align-self: center;
    margin-right: 1rem;
    -webkit-animation-name: spin;
            animation-name: spin;
    -webkit-animation-duration: 2000ms;
            animation-duration: 2000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.error-label {
    margin-top: 0.1rem;
}

.toast {
    font-family: 'Merienda', cursive !important;
}

@-webkit-keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


@media only screen and (min-width: 600px) and (max-width: 1149px) {
    .contact-form {
        width: 75%;
    }

    div.Typist {
        max-width: 75%;
        width: 75%;
    }
}

@media only screen and (min-width: 100px) and (max-width: 599px) {
    .contact-form {
        width: 100%;
    }

    .intro-paragraph-container {
        margin: 0 1rem 0 1rem;
    }


    div.Typist {
        max-width: 100%;
        width: 100%;
    }

    .form-container {
        margin: 0 1rem 0 1rem;
    }
}


